import { Box, Button, Card, Chip, Container, Grid, Typography, useMediaQuery, useTheme } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { FC, useEffect, useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import types from 'src/data/types'
import { Recruit } from 'src/models/Recruit'

interface Club {
  name: string
  type: string
  category: string
  location: string
  description: string
}

const Intro: FC<void> = () => {
  const [club, setClub] = useState<Club>()
  const [recruitInfo, setRecruitInfo] = useState<Recruit>()
  const params = useParams()
  const navigate = useNavigate()
  const { enqueueSnackbar } = useSnackbar()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const authUser = useAuthUser()

  const startAt = recruitInfo ? new Date(recruitInfo.startAt) : new Date()
  const endAt = recruitInfo ? new Date(recruitInfo.endAt) : new Date()
  const today = new Date()

  // 마감일 23시 59분 59초까지 지원할 수 있도록 하루 더함
  const endAtNextDay = recruitInfo ? new Date(recruitInfo.endAt) : new Date()
  endAtNextDay.setDate(endAtNextDay.getDate() + 1)

  const isRecruitingNow = recruitInfo ? startAt <= today && today <= endAtNextDay : false

  const isMember = authUser()?.clubs.filter(c => c.id === parseInt(params.id)).length > 0

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club?clubId=${params.id}`)
      .then((res: AxiosResponse<Club>) => {
        setClub(res.data)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response.data?.message}`, { variant: 'error' })
        navigate(-1)
      })

    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/recruit?clubId=${params.id}`)
      .then((res: AxiosResponse<Recruit>) => {
        setRecruitInfo(res.data)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response.data?.message}`, { variant: 'error' })
      })
  }, [])

  return (
    <>
      <Helmet>
        <title>프로필</title>
      </Helmet>

      <>
        <PageTitleWrapper>
          <Grid container>
            {club && (
              <Grid item xs={12} md={9}>
                <Typography variant="h3">{club.name}</Typography>
                <Typography variant="subtitle2" gutterBottom>
                  {club.description}
                </Typography>
                <Box>
                  {types[club.type] && (
                    <Chip
                      label={types[club.type]}
                      size="small"
                      sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }}
                    />
                  )}
                  {club.category && (
                    <Chip
                      label={club.category}
                      size="small"
                      sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11, marginLeft: 1 }}
                    />
                  )}
                  {club.location && (
                    <Chip
                      label={club.location}
                      size="small"
                      sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11, marginLeft: 1 }}
                    />
                  )}
                </Box>
              </Grid>
            )}

            {recruitInfo && (
              <Grid item xs={12} md={3} mt={1} display="flex" flexDirection="column" justifyContent="center">
                <Box>
                  <Typography variant="h4">
                    모집 시작 <Typography display="inline">{startAt.toLocaleDateString()}</Typography>
                  </Typography>
                </Box>
                <Box>
                  <Typography variant="h4" display="inline">
                    모집 종료 <Typography display="inline">{endAt.toLocaleDateString()}</Typography>
                  </Typography>
                </Box>
                <Box>
                  <Typography variant="h4" display="inline">
                    정원 <Typography display="inline">{recruitInfo.limit}명</Typography>
                  </Typography>
                </Box>
                <Chip
                  label={isRecruitingNow ? '모집 중' : '모집 기간이 아니에요'}
                  sx={{ padding: theme.spacing(0.5), marginY: 1 }}
                  color={isRecruitingNow ? 'success' : 'warning'}
                />
              </Grid>
            )}
          </Grid>
        </PageTitleWrapper>

        <Container maxWidth="md">
          {recruitInfo && recruitInfo.contents ? (
            <>
              <Card>
                <Box p={2} overflow="auto">
                  <div className="view ql-editor" dangerouslySetInnerHTML={{ __html: recruitInfo.contents }}></div>
                </Box>
              </Card>
              <Box textAlign="center" my={2}>
                <Button
                  variant="contained"
                  disabled={!isRecruitingNow || isMember}
                  sx={{ paddingX: 5 }}
                  onClick={() => navigate('apply')}
                >
                  지원하기
                </Button>
              </Box>
            </>
          ) : (
            <Card>
              <Box height="50vh" display="flex" justifyContent="center" alignItems="center">
                <Typography variant="h4">동아리에서 올린 소개글이 없어요 ㅠ.ㅠ</Typography>
              </Box>
            </Card>
          )}
        </Container>
      </>
    </>
  )
}

export default Intro
